.screen {
  background-color: #555;
  position: relative;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 5px 10px;
  border-radius: 8px;
}

.screen__lcd {
  font-family: 'OPTICalculator';
  font-size: 56px;
  color: #001;
  background-color: #d1e3e4;
  margin: 15px 15px 22px;
  padding: 0 10px;
  height: 95px;
  line-height: 95px;
  text-align: right;
  border-radius: 5px;
  -webkit-box-shadow: -3px -3px 3px 0 #222, 3px 3px 3px 0 #888;
          box-shadow: -3px -3px 3px 0 #222, 3px 3px 3px 0 #888;
  overflow: hidden;
}



