.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 15px;
  -ms-flex-align: center;
      align-items: center;
}

.header__logo {
  width: 80px;
}

.header__logo path {
  fill: #555;
}

.header__panel {
  width: 145px;
  height: 45px;
  background-color: #271a17;
  border-radius: 8px;
  border: 4px solid #555;
  position: relative;
  -webkit-box-shadow: inset 1px 1px 0 #222, inset -1px -1px 0 #888;
          box-shadow: inset 1px 1px 0 #222, inset -1px -1px 0 #888;
}

.header__panel::after {
  content: 'TWO WAY POWER';
  position: absolute;
  bottom: -15px;
  font-size: 9px;
  text-align: center;
  left: 0;
  right: 0;
  font-weight: bold;
  letter-spacing: .1em;
  -webkit-transform: scale(1, .7);
      -ms-transform: scale(1, .7);
          transform: scale(1, .7);
}



